/* :root font added: 
  --system-ui: system-ui, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
  https://css-tricks.com/snippets/css/system-font-stack/
 */

/* Personal changes
    :root - box-sizing: border-box;
    input - appearance: none;
    body - margin: 0;
*/

html {
  font-size: 62.5%;
}

body {
  margin: 0;
}

:root {
  line-height: 1.5;
  --system-ui: system-ui, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
  box-sizing: border-box;
}

h1, h2, h3, h4, h5, figure, p, ol, ul {
  margin: 0;
}

ol, ul {
  list-style: none;
  padding-inline: 0;
}

h1, h2, h3, h4, h5 {
  font-size: inherit;
  font-weight: inherit;
}

img {
  display: block;
  max-inline-size: 100%;
}

/* New styles below */
:root {
  --background-color: rgb(227, 227, 227);
  --orange: rgba(255, 132, 0, 0.495);
}

html {
  font-family: sans-serif;
  background-color: var(--background-color);
}

nav {
  display: block;
  text-align: center;
  margin: 10px 0 0 0
}

nav button {
  padding: 5px 15px;
  border: none;
  border-radius: 10px;
  margin: 0 5px;
  background-color: var(--orange);
  font-size: 2rem;
}

#content {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.home, .menu, .about {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
}

p, li {
  font-size: 2rem;
}

.outer {
  margin: 0 0 10px 0;
}

.inner {
  background-color: white;
  padding: 10px;
  border-radius: 10px;
  width: 600px;
  border: 1px solid var(--orange);
  box-shadow: 3px 2px 10px rgb(171, 170, 170)
}

#logo{
  display: inline-block;
}

.logo-container-inner {
  background-color: var(--background-color);
  border: none;
  text-align: center;
  box-shadow: none;
}

.menu img {
  object-fit: cover;
  width: 100%;
  height: 100%;
  object-position: center;
  margin-left: 22.5px;
  transform: scale(1.2)
}

.menu .inner {
  overflow: hidden;
  width: 725px;
  height: 650px;
}

.menu .outer {
  overflow: hidden;
}

.menu-1-container-outer {
  margin-top: 10px;
}

button {
  cursor: pointer
}`, "",{"version":3,"sources":["webpack://./src/styles.css"],"names":[],"mappings":"AAAA,4EAA4E;;AAE5E;;;EAGE;;AAEF;;;;CAIC;;AAED;EACE,gBAAgB;AAClB;;AAEA;EACE,SAAS;AACX;;AAEA;EACE,gBAAgB;EAChB,kIAAkI;EAClI,sBAAsB;AACxB;;AAEA;EACE,SAAS;AACX;;AAEA;EACE,gBAAgB;EAChB,iBAAiB;AACnB;;AAEA;EACE,kBAAkB;EAClB,oBAAoB;AACtB;;AAEA;EACE,cAAc;EACd,qBAAqB;AACvB;;AAEA,qBAAqB;AACrB;EACE,sCAAsC;EACtC,kCAAkC;AACpC;;AAEA;EACE,uBAAuB;EACvB,yCAAyC;AAC3C;;AAEA;EACE,cAAc;EACd,kBAAkB;EAClB;AACF;;AAEA;EACE,iBAAiB;EACjB,YAAY;EACZ,mBAAmB;EACnB,aAAa;EACb,+BAA+B;EAC/B,eAAe;AACjB;;AAEA;EACE,aAAa;EACb,sBAAsB;EACtB,mBAAmB;AACrB;;AAEA;EACE,aAAa;EACb,sBAAsB;EACtB,mBAAmB;EACnB,WAAW;AACb;;AAEA;EACE,eAAe;AACjB;;AAEA;EACE,kBAAkB;AACpB;;AAEA;EACE,uBAAuB;EACvB,aAAa;EACb,mBAAmB;EACnB,YAAY;EACZ,+BAA+B;EAC/B;AACF;;AAEA;EACE,qBAAqB;AACvB;;AAEA;EACE,yCAAyC;EACzC,YAAY;EACZ,kBAAkB;EAClB,gBAAgB;AAClB;;AAEA;EACE,iBAAiB;EACjB,WAAW;EACX,YAAY;EACZ,uBAAuB;EACvB,mBAAmB;EACnB;AACF;;AAEA;EACE,gBAAgB;EAChB,YAAY;EACZ,aAAa;AACf;;AAEA;EACE,gBAAgB;AAClB;;AAEA;EACE,gBAAgB;AAClB;;AAEA;EACE;AACF","sourcesContent":["/* https://mattbrictson.com/blog/css-normalize-and-reset#my-recommendation */\n\n/* :root font added: \n  --system-ui: system-ui, \"Segoe UI\", Roboto, Helvetica, Arial, sans-serif, \"Apple Color Emoji\", \"Segoe UI Emoji\", \"Segoe UI Symbol\";\n  https://css-tricks.com/snippets/css/system-font-stack/\n */\n\n/* Personal changes\n    :root - box-sizing: border-box;\n    input - appearance: none;\n    body - margin: 0;\n*/\n\nhtml {\n  font-size: 62.5%;\n}\n\nbody {\n  margin: 0;\n}\n\n:root {\n  line-height: 1.5;\n  --system-ui: system-ui, \"Segoe UI\", Roboto, Helvetica, Arial, sans-serif, \"Apple Color Emoji\", \"Segoe UI Emoji\", \"Segoe UI Symbol\";\n  box-sizing: border-box;\n}\n\nh1, h2, h3, h4, h5, figure, p, ol, ul {\n  margin: 0;\n}\n\nol, ul {\n  list-style: none;\n  padding-inline: 0;\n}\n\nh1, h2, h3, h4, h5 {\n  font-size: inherit;\n  font-weight: inherit;\n}\n\nimg {\n  display: block;\n  max-inline-size: